@font-face {
  font-family: "Exo2.0-Thin";
  font-display: fallback;
  src: url(/funbox/static/media/Exo2.0-Thin.44bde874.otf);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Product {
  position: relative;
  width: 320px;
  margin: 0 40px;
}

.Product-Wrapper {
  position: relative;
  z-index: 9999;
  height: 480px;
  padding-left: 48px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-radius: 3%;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-clip-path: polygon(50px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
          clip-path: polygon(50px 0, 100% 0, 100% 100%, 0 100%, 0 50px);
}

.Product-Wrapper:before {
  content: "";
  position: absolute;
  z-index: 9999;
  left: -35px;
  top: -30px;
  width: 70px;
  height: 60px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-bottom: 4px solid #1698d9;
}

.Product-Title {
  margin: 0;
  font-size: 48px;
  font-weight: 700;
}

.Product-Subtitle {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
}

.Product-Benefit-list {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding-left: 0;
  padding-top: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.Product-Benefit-item {
  display: -webkit-flex;
  display: flex;
  color: #666666;
}

.Product-Image {
  position: absolute;
  bottom: 0;
  left: 0;
}

.Product-Weight {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 81px;
  height: 81px;
  z-index: 1;
  margin: 0;
  font-size: 42px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
  background-color: #1698d9;
  border-radius: 50%;
}
.Product-Weight-kg {
  font-size: 21px;
}

.Product-Wrapper_disabled {
  border: 4px solid #b3b3b3;
  cursor: not-allowed;
}
.Product-Wrapper_disabled:before {
  border-bottom: 4px solid #b3b3b3;
}
.Product-Wrapper_disabled::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  display: block;
  width: 100%;
  height: 100%;
  border-bottom: 4px solid #b3b3b3;
  background: rgb(199, 210, 211);
  opacity: 0.5;
}
.Product-Weight_disabled {
  background-color: #b3b3b3;
  color: #ffffff;
}

.Product-Title_disabled {
  opacity: 0.5;
}

.Product-Subtitle_disabled {
  opacity: 0.5;
}

.Product-Wrapper_selected {
  border: 4px solid #d91667;
}
.Product-Wrapper_selected:before {
  border-bottom: 4px solid #d91667;
}

.Product-Weight_selected {
  background-color: #d91667;
}

.Product-Wrapper_hover {
  border: 4px solid #2ea8e6;
}
.Product-Wrapper_hover:before {
  border-bottom: 4px solid #2ea8e6;
}

.Product-Weight_hover {
  background-color: #2ea8e6;
}

.Product-Wrapper_selectedHover {
  border: 4px solid #e62e7a;
}
.Product-Wrapper_selectedHover:before {
  border-bottom: 4px solid #e62e7a;
}

.Product-Weight_selectedHover {
  background-color: #e62e7a;
}

.ProductDescription {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #ffffff;
  text-shadow: 0 1px 0 #000000;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
}

.ProductDescription-Button {
  color: #1698d9;
  display: inline;
  padding: 0;
  font-weight: 700;
  background: none;
  border: none;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer;
}

.ProductText {
  color: #666666;
  font-size: 16px;
  font-weight: 400;
  margin: 0;
  padding-top: 25px;
}

.ProductText_pink {
  color: #e62e7a;
}

.Products {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0;
  margin: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  background-image: url(/funbox/static/media/Background.70be3938.jpg);
  background-size: cover;
  overflow-x: hidden;
}

.App-Header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-shadow: 0 1px 0 #000000;
  color: #ffffff;
  font-family: "Exo2.0-Thin";
  font-size: 36px;
  font-weight: 400;
}

